import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { HttpClient} from '@angular/common/http';



@Injectable()
export class DboHelpDevService {
  private databaseurlDev = '/api/dev/main/v1.0/database-objects';
  constructor(private rest: HttpClient) { }
  public sessionId: string;

/**
 * æ°æ®åºå¯¹è±¡éæ©å¸®å©çé¢ï¼è®¾è®¡æ¶ï¼
 */
  getAllDatabaseObject(relativePath: string): Observable<any> {
   
    const url =  this.databaseurlDev + '?relativePath=' + relativePath;
    return this.rest.get(url).pipe( );
  }


/**
 * æ°æ®åºå¯¹è±¡è¯¦ç»ä¿¡æ¯éæ©å¸®å©çé¢
 */
  getCurrentDatabaseObject(relativePath: string, databaseobjectid: string): Observable<any> {
    const url = this.databaseurlDev + '/single?relativePath=' + relativePath
     + '&databaseobjectid=' + databaseobjectid;
    return this.rest.get(url).pipe( );
  }

}

